import React from 'react';

export default function Form(props) {
    const { cartItems, finalPrice } = props;

    const submitHandler = (e) => {
        var osoite = document.getElementById("osoite").value;

        const items = cartItems.map(cartItem => cartItem.nimi);
        const stringi = items.reduce((a, b) =>  a + ', '+ b);
        alert(
            'Kiitos tilauksestasi!' + "\r\n" +
            'Toimitus osoite => ' + osoite +  "\r\n" +
            'Lopullinen hinta => ' + finalPrice.toFixed(2)+'€' + "\r\n" +
            'Tilatut tuotteet => ' + stringi
        );
        e.preventDefault();
        window.location.reload(false);
    }

    return <form onSubmit={submitHandler} className="block-2">
        <h2>Tilauksen vahvistaminen</h2>

        <div className="row">
            <input type="text" id="nimi" placeholder="Nimi" required></input>
        </div>
        <div className="row">
            <input type="email" id="email" placeholder="Sähköposti" required></input>
        </div>
        <div className="row">
            <input type="tel" id="puhnro" placeholder="Puhelin numero" required></input>
        </div>
        <div className="row">
            <input type="address" id="osoite" placeholder="Toimitus osoite" required></input>
        </div>
        <div className="row">
            <input type="postal" id="postalcode" placeholder="Postinumero" required></input>
        </div>
        <div className="row">
            <input type="text" id="postitoimipaikka" placeholder="Postitoimipaikka" required></input>
        </div>
        <div className="row">
            <button type="submit" id="submit">Vahvista tilaus</button>
        </div>
    </form>;
};