import Otsikko from './components/Otsikko';
import Main from './components/Main';
import Ostoskori from './components/Ostoskori';
import data from './data'
import { useState } from 'react';
import Form from './components/vahvistus';

function App() {
  const { tuotteet } = data;
  const [cartItems, setCartItems] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [finalPrice, setFinalPrice] = useState(0);

  const näytäForm = () => {
    setShowForm(!showForm);
  }
  const onAdd = (tuote) => {
    const exist = cartItems.find(x => x.id === tuote.id);
    if (exist) {
      setCartItems(cartItems.map(x => x.id === tuote.id ? { ...exist, qty: exist.qty + 1 } : x
      )
      );
    } else {
      setCartItems([...cartItems, { ...tuote, qty: 1 }]);
    }
  }
  const onRemove = (tuote) => {
    const exist = cartItems.find((x) => x.id === tuote.id);
    if (exist.qty === 1) {
      setCartItems(cartItems.filter((x) => x.id !== tuote.id));
    } else {
      setCartItems(cartItems.map(x => x.id === tuote.id ? { ...exist, qty: exist.qty - 1 } : x
      )
      );

    }
  }
  return (
    <div className="App">
      <Otsikko countCartItems={cartItems.length}></Otsikko>
      <div className="row">
        <Main onAdd={onAdd} tuotteet={tuotteet}></Main>
        <Ostoskori setFinalPrice={setFinalPrice} onRemove={onRemove} onAdd={onAdd} näytäForm={näytäForm} cartItems={cartItems}></Ostoskori>
      </div>
      {cartItems.length !== 0 && showForm && (
        <div>
          <Form cartItems={cartItems} finalPrice={finalPrice}></Form>
        </div>
      )}
    </div>
  );
}

export default App;
