import React from 'react';

export default function Ostoskori(props) {
    const { cartItems, onAdd, onRemove, näytäForm, setFinalPrice } = props;
    const tavaroidenHinta = cartItems.reduce((a, c) => a + c.hinta * c.qty, 0);
    const veroHinta = tavaroidenHinta * 0.24;
    const kuljetusHinta = tavaroidenHinta > 500 ? 0 : 39.90;
    const kokonaisHinta = tavaroidenHinta + veroHinta + kuljetusHinta;
    setFinalPrice(kokonaisHinta);
    
    return <aside className="block col-1">
        <h2>Ostokorin tavarat</h2>
        <div>
            {cartItems.length === 0 && <div>Ostoskori on tyhjä</div>}
        </div>
        {cartItems.map((item) => (
            <div key={item.id} className="row">
                <div className="col-2">{item.nimi}</div>
                <div>
                    <button onClick={() => onAdd(item)} className="add">
                        +
                    </button>
                    <button onClick={() => onRemove(item)} className="remove">
                        -
                    </button>
                </div>
                <div className="col-2 text-right">
                    {item.qty} x €{item.hinta.toFixed(2)}
                </div>
            </div>
        ))}
        {cartItems.length !== 0 && (
            <>
                <hr></hr>
                <div className="row">
                    <div className="col-2">Tuotteiden hinta</div>
                    <div className="col-1 text-right">€{tavaroidenHinta.toFixed(2)}</div>
                </div>
                <div className="row">
                    <div className="col-2">Veron hinta</div>
                    <div className="col-1 text-right">€{veroHinta.toFixed(2)}</div>
                </div>
                <div className="row">
                    <div className="col-2">Kuljetuksen hinta</div>
                    <div className="col-1 text-right">€{kuljetusHinta.toFixed(2)}</div>
                </div>
                <div className="row">
                    <div className="col-2"><strong>Yhteensä</strong></div>
                    <div className="col-1 text-right"><strong>€{kokonaisHinta.toFixed(2)}</strong></div>
                </div>
                <hr/>
                <div className="row">
                    <button onClick={() =>näytäForm()}>Vahvista tilaus</button>
                </div>
            </>
        )}
    </aside>
}