import React from 'react'

export default function Tuote(props) {
    const { tuote, onAdd } = props;
    return (
        <div>
            <img className="small" src={tuote.kuva} alt={tuote.nimi}></img>
            <h3>{tuote.nimi}</h3>
            <div>€{tuote.hinta}</div>
            <div>
                <button onClick={() => onAdd(tuote)}>Lisää koriin</button>
            </div>
        </div>
    )
}
