import React from 'react';
import Tuote from './Tuote';

export default function Main(props) {
    const { tuotteet, onAdd } = props;
    return <main className=" block col-2">
        <h2>Tuotteet</h2>
        <div className="row">
            {tuotteet.map((tuote) => (
                <Tuote key={tuote.id} tuote={tuote} onAdd={onAdd}></Tuote>
            ))}
        </div>
    </main>;
}